.fileUpload {
	background-color: #ffffff;
	width: 600px;
	margin: 0 auto;
	padding: 20px;
}

.fileUploadBtn {
	width: 100%;
	margin: 0;
	color: #fff;
	background: #1fb264;
	border: none;
	padding: 10px;
	border-radius: 4px;
	border-bottom: 4px solid #15824b;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
	outline: none;
	text-transform: uppercase;
	font-weight: 700;
}

.fileUploadBtn:hover {
	background: #1aa059;
	color: #ffffff;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
	cursor: pointer;
}

.fileUploadBtn:active {
	border: 0;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
}

.fileUploadContent {
	display: none;
	text-align: center;
}

.fileUploadInput {
	position: absolute;
	margin: 0;
	padding: 0;
	width: 100%;
	height: 100%;
	outline: none;
	opacity: 0;
	cursor: pointer;
}

.imageUploadWrap {
	margin-top: 20px;
	border: 4px dashed #1fb264;
	position: relative;
}

.imageDropping,
.imageUploadwrap:hover {
	background-color: #1fb264;
	border: 4px dashed #ffffff;
}

.imageTitleWrap {
	padding: 0 15px 15px 15px;
	color: #222;
}

.dragText {
	text-align: center;
}

.dragText h3 {
	font-weight: 100;
	text-transform: uppercase;
	color: #15824b;
	padding: 60px 0;
}

.fileUpload-image {
	max-height: 200px;
	max-width: 200px;
	margin: auto;
	padding: 20px;
}

.removeImage {
	width: 200px;
	margin: 0;
	color: #fff;
	background: #cd4535;
	border: none;
	padding: 10px;
	border-radius: 4px;
	border-bottom: 4px solid #b02818;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
	outline: none;
	text-transform: uppercase;
	font-weight: 700;
}

.removeImage:hover {
	background: #c13b2a;
	color: #ffffff;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
	cursor: pointer;
}

.removeImage:active {
	border: 0;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
}

