	border: 1px solid #397547;
	border-radius: 5px;
	display: inline-block;
	width: 200px;
	height: 40px;
	background-color: #397547;
	color: #fff;
	font-size: 20px;
	overflow: hidden;
}

.file-input-wrapper:hover > .btn-file-input {
	background-color: #ffffff;
	color: #397547;
}

